<template>
    <div class="generate-barcode">
        <div class="head">
            <el-button type="primary" @click="add">添加</el-button>
            <el-select class="m-l-10" filterable v-model="orgNo" placeholder="请选择">
                <el-option v-for="item in options" :key="item.value" :label="item.label + item.value" :value="item.value"> </el-option>
            </el-select>
            <el-select v-if="orgNo === '41'" class="m-l-10" filterable v-model="hnCity" placeholder="请选择">
                <el-option v-for="item in hnList" :key="item.value" :label="item.label + item.value" :value="item.value"> </el-option>
            </el-select>
        </div>
        <div class="box-wp flex-row flex-wrap flex1">
            <section class="box" v-for="(item, index) in list" :key="index">
                <svg :id="item.id" class="svg-ele" :class="{ 'opacity-0': !item.checked }"></svg>
                <div class="flex-row-vcenter">
                    <el-input
                        maxlength="100"
                        show-word-limit
                        v-model="item.value"
                        placeholder="请输入内容"
                        clearable
                        @input="createCode(index)"
                        @clear="createCode(index)"
                    ></el-input>
                </div>
                <div class="flex-row-vcenter m-t-10 space-x-10">
                    <el-switch v-model="item.checked"></el-switch>
                    <i
                        class="rotate linear cursor-pointer ft-20"
                        :class="item.showRandomBtn ? 'el-icon-arrow-down' : 'el-icon-arrow-up'"
                        @click="item.showRandomBtn = !item.showRandomBtn"
                    ></i>
                    <i class="el-icon-copy-document cursor-pointer ft-20" @click="copy(item.value)"></i>
                </div>

                <el-collapse-transition>
                    <div v-if="item.showRandomBtn">
                        <div class="img-wrapper m-t-10 space-x-10">
                            <img src="@/assets/images/meter.png" @click="randemDeviceNo(index, '01')" />
                            <img src="@/assets/images/transformer.png" @click="randemDeviceNo(index, '02')" />
                            <img src="@/assets/images/box.png" @click="randemDeviceNo(index, '05')" />
                            <img src="@/assets/images/terminal.png" @click="randemDeviceNo(index, '09')" />
                        </div>
                        <p class="m-t-10">
                            <el-button-group>
                                <el-button @click="randemDeviceNo(index, '61')">导轨表</el-button>
                                <el-button @click="randemDeviceNo(index, '62')">量测开关</el-button>
                                <el-button @click="randemDeviceNo(index, '71')">封印</el-button>
                            </el-button-group>
                        </p>
                    </div>
                </el-collapse-transition>
            </section>
        </div>

        <div class="footer">
            <div v-for="(item, index) in srcList2" :key="index">
                <el-image style="width: 70px; height: 70px" :src="item.imgSrc" :preview-src-list="previewSrcList" fit="contain"> </el-image>
                <div style="font-size: 12px; text-align: center">{{ item.label }}</div>
            </div>
        </div>
    </div>
</template>

<script>
import JsBarcode from 'jsbarcode';
import { createCheckCode } from '@/utils/check-code';
import { clipboard } from '@/utils/util';
import orgList from './org-list';
import hnList from './hn-list';
let id = 1;

export default {
    data() {
        return {
            list: [
                {
                    value: '',
                    id: 'barcode' + id++,
                    checked: true,
                    showRandomBtn: true,
                },
            ],
            orgNo: '41',
            hnCity: '01',
            options: orgList,
            hnList: hnList,

            srcList: [
                './img/example-box-outer1.jpg',
                './img/example-box-outer3.jpg',
                './img/example-box-inner1.jpg',
                './img/example-box-inner2.jpg',
                './img/example-box-inner3.jpg',
                './img/example-meter1.jpg',
                './img/example-switch1.jpg',
                './img/example-switch2.jpg',
                './img/example-switch3.jpg',
            ],
            srcList2: [
                { label: '计量箱外观', imgSrc: './img/example-box-outer3.jpg' },
                { label: '计量箱内部', imgSrc: './img/example-box-inner3.jpg' },
                { label: '电能表', imgSrc: './img2/example-meter.png' },
                { label: '开关', imgSrc: './img2/example-switch.png' },
                { label: '互感器', imgSrc: './img2/example-it.png' },
                { label: '采集终端', imgSrc: './img2/example-tmnl.png' },
                { label: '导轨表', imgSrc: './img2/example-guide.png' },
                { label: '量测开关', imgSrc: './img2/example-sam1.png' },
            ],
        };
    },
    computed: {
        previewSrcList() {
            return this.srcList2.map(ele => ele.imgSrc);
        },
    },
    mounted() {
        this.createCode(0);
    },
    methods: {
        /**
         * 添加一项
         */
        add() {
            this.list.push({
                value: '',
                id: 'barcode' + id++,
                checked: true,
                showRandomBtn: true,
            });
        },
        /**
         * 生成条码
         */
        createCode(index) {
            let { value, id: curId } = this.list[index];
            if (!value) {
                value = '123';
            }
            JsBarcode(document.getElementById(curId), value, {
                text: this.setGapValue(value),
                // font: '',
            });
        },
        /**
         * 生成随机条码
         */
        randemDeviceNo(index, type) {
            let value = this.orgNo + '300' + type + '0' + Date.now();
            if (type === '71') {
                // 封印16位
                value = this.orgNo + Date.now();
            }
            if (this.orgNo === '41' && type === '05') {
                let timeStampStr = String(Date.now());
                value = this.orgNo + '300' + type + '0' + timeStampStr.substring(0, 1) + this.hnCity + timeStampStr.substring(3);
            }
            value = createCheckCode(value);

            this.list[index].value = value;
            JsBarcode(document.getElementById(this.list[index].id), value, {
                text: this.setGapValue(value),
                // font: '',
            });
        },
        /**
         * 复制
         */
        copy(item) {
            if (item) {
                clipboard(item);
                this.$message.success('复制成功');
            }
        },
        // 设置间隙
        setGapValue(val) {
            if (val) {
                let arr = [val.substring(0, 5), val.substring(5, 7), val.substring(7, 21), val.substring(21, 23)];
                arr = arr.filter(Boolean);
                let res = arr.join(' ');
                return res;
            } else {
                return val;
            }
        },
    },
};
</script>

