    height: 100%;
    display: flex;
    flex-direction: column;
}

.box-wp {
    margin-left: -10px;
}

.box {
    border: 1px solid #ddd;
    min-width: 340px;
    margin: 10px;
    padding: 10px;
    height: max-content;
}

.svg-ele {
    height: 142px;
    cursor: pointer;
}

.linear {
    transition: all linear 0.5s;
}
.rotate {
    transform: rotate(180deg);
}

.img-wrapper {
    > img {
        width: 60px;
        height: 60px;
        cursor: pointer;
    }

    > img:hover {
        opacity: 0.5;
    }
}

.footer {
    display: flex;
    gap: 5px;
    flex-wrap: wrap;
}
</style>

<style>
.el-image-viewer__mask {
    opacity: 0.85;
}
</style>
